// Angular
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
// Components
import { BaseComponent } from "./views/theme/base/base.component";
import { ErrorPageComponent } from "./views/theme/content/error-page/error-page.component";
// Auth
import { AuthGuard } from "./core/auth";

const routes: Routes = [
	{
		path: "auth",
		loadChildren: () => import("./views/pages/auth/auth.module").then((m) => m.AuthModule),
	},

	{
		path: "",
		component: BaseComponent,
		canActivate: [AuthGuard],
		data: {
			expectedRole: ["administrator", "manager", "spv-tro", "admin-tro", "spv-finance", "admin-finance", "spv-engineer", "engineer", "user", "admin-engineer"],
		},
		children: [
			{
				path: "dashboard",
				canActivate: [AuthGuard],
				data: {
					expectedRole: ["administrator", "spv-tro", "admin-tro", "spv-finance", "admin-finance", "spv-engineer", "admin-engineer", "manager", "engineer", "user", "admin-engineer"],
				},
				loadChildren: () => import("./views/pages/dashboard/dashboard.module").then((m) => m.DashboardModule),
			},
			{
				path: "negotiationCode",
				canActivate: [AuthGuard],
				data: {
					expectedRole: ["manager", "administrator", "spv-finance", "admin-finance"],
				},
				loadChildren: () => import("./views/pages/negotiationCode/negotiationCode.module").then((m) => m.NegotiationCodeModule),
			},
			{
				path: "onlineOrders",
				canActivate: [AuthGuard],
				data: {
					expectedRole: ["manager", "administrator", "spv-engineer", "admin-engineer"],
				},
				loadChildren: () => import("./views/pages/onlineOrders/onlineOrders.module").then((m) => m.OnlineOrdersModule),
			},
			{
				path: "offlineOrders",
				canActivate: [AuthGuard],
				data: {
					expectedRole: ["manager", "administrator", "spv-engineer", "admin-engineer"],
				},
				loadChildren: () => import("./views/pages/offlineOrders/offlineOrders.module").then((m) => m.OfflineOrdersModule),
			},
			{
				path: "error/403",
				component: ErrorPageComponent,
				data: {
					type: "error-v6",
					code: 403,
					title: "403... Access forbidden",
					desc: "Looks like you don't have permission to access for requested page.<br> Please, contact administrator",
				},
			},
			{ path: "error/:type", component: ErrorPageComponent },
			{ path: "", redirectTo: "/dashboard", pathMatch: "full" },
			{ path: "**", redirectTo: "/dashboard", pathMatch: "full" },
		],
	},

	{ path: "**", redirectTo: "error/403", pathMatch: "full" },
];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule],
})
export class AppRoutingModule {}
